body{
  margin: 0 ;
  align-items: center;
  padding: 0 ;
  width:100%;
  display: contents;
}

.mainPage {
  justify-content: center ;
  align-items: center ;
}

.mainContainer {
  display: flex;
  justify-content: center;
  align-items: center ;
  width: 100%;
}

.dataContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header {
  text-align: center;
  font-size: 32px;
  font-weight: 600;
}

.bio {
  text-align: center;
  color: gray;
  margin-top: 16px;
}

.loading-spinner .spinner-border {
  width: 20%; /* Adjust the width to your desired size */
  height: 20%; /* Adjust the height to your desired size */
}

.waveButton {
  cursor: pointer;
  margin-top: 16px;
  padding: 8px;
  border: 0;
  border-radius: 5px;
  
}

.contentNav {
  justify-content: space-between; /* Align items at each end */
  align-items: center; /* Center items vertically */
}

.buttonsNav {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}